<template>
  <div>
    <div class="loading" v-show="isloading == true">
      <img src="../../img/猫吃猫.gif" class="loadingpic" />
      <p class="loadingWrite">loading...</p>
    </div>

    <div :class="[isNight ? 'readNight' : 'read']"   v-show="isloading == false">
      <div class="head">
        <div class="shang">
          <img src="../../img/返回灰.png" @click="$router.back()" />
        </div>
        <div class="section">
          <ul>
            <router-link to="/">
              <li>
                <img src="../../img/首页.png" alt="" />
                <p>首页</p>
              </li>
            </router-link>
            <li
              @click="removeBookrack(currentBook)"
              v-if="bookrack.find((id) => id == currentBook)"
            >
              <img src="../../img/减.png" alt="" />
              <p>移出书架</p>
            </li>
            <li @click="addBookrack(currentBook)" v-else>
              <img src="../../img/加.png" alt="" />
              <p>加入书架</p>
            </li>
            <li @click="showPopup">
              <img src="../../img/充值.png" alt="" />
              <p>充值</p>
            </li>
            <li @click="night">
              <img src="../../img/夜间模式.png" alt="" />
              <p>夜间模式</p>
            </li>
            <li @click="pre" style="float:left">
              <img src="../../img/左.png" alt="">
              <p>上一章</p>
              
            </li>
            <li @click="next" style="float:left">
              <img src="../../img/右.png" alt="">
              <p> 下一章</p>
            </li>
            
          </ul>
        </div>
      </div>
      <h3 :style="isNight?  'color:#b57474' :'color:#222' ">{{ title }}</h3>

      <p
        :class="contents.length == 1 ? 'vipParagraph' : 'paragraph'"
        :style="isNight?  'color:#b57474' :'color:#222' "
        v-for="(item, index) in contents"
        :key="index"
      >
        {{ item }}
      </p>

      <div class="toggle">
        <span class="pre" @click="pre">←上一章</span>
        <span class="next" @click="next">下一章→</span>
      </div>
      <p class="vip" v-if="contents.length == 1">以下内容需要VIP特权才可观看</p>
    </div>
    <van-popup v-model="show">醒醒吧，你哪有钱？？？</van-popup>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  activated() {
    this.currentBook = this.$route.query.currentBook;

    this.title = this.$route.query.chapters[this.chapterIndex].title;

    this.chapterIndex = parseInt(this.$route.query.chapterIndex);
    this.isloading = true;
    this.axios
      .get(
        "https://novel.kele8.cn/chapters/" +
          encodeURIComponent(this.$route.query.chapters[this.chapterIndex].link)
      )
      .then((res) => {
        // console.log(res.data.chapter.cpContent);
        this.content = res.data.chapter.cpContent;
      })
      .finally(() => {
        this.isloading = false;
      });
  },
  data() {
    return {
      content: null,
      contents: [],
      isloading: true,
      currentBook: "",
      show: false,
      chapterIndex: 0,
      title: "",
      isNight:false,
    };
  },
  watch: {
    content: function (n) {
      this.contents = n.split("\n");
    },
    chapterIndex: function (n) {
      // console.log(n)
      if (!n) {
        return;
      } else {
        this.isloading = true;
        this.title = this.$route.query.chapters[this.chapterIndex].title;
        this.axios
          .get(
            "https://novel.kele8.cn/chapters/" +
              encodeURIComponent(
                this.$route.query.chapters[this.chapterIndex].link
              )
          )
          .then((res) => {
            // console.log(res.data.chapter.cpContent);
            this.content = res.data.chapter.cpContent;
          })
          .finally(() => {
            this.isloading = false;
          });
      }
    },
  },
  methods: {
    ...mapMutations(["addBookrack", "removeBookrack"]),
    showPopup() {
      this.show = true;
    },
    pre: function () {
      if (this.chapterIndex == 0) {
        return;
      } else {
        console.log("上一章");
        this.chapterIndex--;
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      }
    },
    next: function () {
      console.log("下一章");
      this.chapterIndex++;
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    night: function () {
      this.isNight=!this.isNight
    },
  },
  computed: mapState(["bookrack"]),
};
</script>

<style scoped lang="less">
div {
  .read {
    background: #f7f5f2;

    .head {
      width: 100%;
      height: 56px;
      background: #fff;
      position: fixed;
      z-index: 9;
      top:0px;
      .shang {
        float: left;

        img {
          // display: -webkit-box;
          height: 18px;
          margin-top: 18px;
          margin-left: 5px;
        }
      }
      .section {
        // background: blue;
        width: 90%;
        float: right;
        height: 100%;
        ul {
          height: 100%;
          &::after {
            display: block;
            content: "";
            clear: both;
          }
          li {
            float: right;
            width: 16%;
            height: 100%;
            text-align: center;
            margin-top: 1px;
            img {
              width: 25px;
              margin-top: 5px;
            }
            p {
              margin: 0;
              padding: 0;
              text-align: center;
              font-size: 12px;
            }
          }
        }
      }
    }
  }
}
.paragraph {
  color: #222;
  text-align: justify;
  text-indent: 1em;

  width: 80%;
  margin: auto;
  font-size: 16px;
  font-family: "Arial", "Hiragino Sans GB", "Microsoft YaHei",
    "WenQuanYi Micro Hei", sans-serif;
  margin-bottom: 36px;
  white-space: normal;
  word-break: break-all;
  overflow: hidden;
}

.vipParagraph {
  color: #222;
  text-align: justify;
  // text-indent: 2em;

  width: 80%;
  margin: auto;
  font-size: 16px;
  font-family: "Arial", "Hiragino Sans GB", "Microsoft YaHei",
    "WenQuanYi Micro Hei", sans-serif;
  margin-bottom: 36px;
  white-space: normal;
  word-break: break-all;
  overflow: hidden;
  filter: blur(4px);
}

h3 {
  width: 80%;
  margin: auto;
  margin-top: 56px;
  margin-bottom: 20px;
  position: relative;
  padding-top: 20px;
}
.vip {
  color: red;
  font-weight: 700;
  font-size: 24px;
  text-align: center;
  position: absolute;
  top: 300px;
  left: 10%;
}
.loadingpic {
  width: 200px;
  height: 200px;
}

.loadingpic {
  width: 200px;
  height: 200px;
}
.loading {
  position: absolute;
  top: 40%;
  left: 25%;
  .loadingWrite {
    text-align: center;
    font-size: 14px;
  }
}
.van-popup {
  width: 70%;
  height: 100px;
  border-radius: 10px;
  text-align: center;
  line-height: 100px;
  font-weight: 600;
  padding: 15px;
}
.pre {
  float: left;
  color: #666;
}
.next {
  float: right;
  color: #666;
}
.toggle {
  height: 40px;
}
.section {
  ul {
    li {
      color: #666;
    }
  }
}
.readNight {
    background: #000;

    .head {
      width: 100%;
      height: 56px;
      background: #000;
      position: fixed;
      z-index: 9;
      top:0px;
      .shang {
        float: left;

        img {
          // display: -webkit-box;
          height: 18px;
          margin-top: 18px;
          margin-left: 5px;
        }
      }
      .section {
        // background: blue;
        width: 90%;
        float: right;
        height: 100%;
        ul {
          height: 100%;
          &::after {
            display: block;
            content: "";
            clear: both;
          }
          li {
            float: right;
            width: 16%;
            height: 100%;
            text-align: center;
            margin-top: 1px;
            img {
              width: 25px;
              margin-top: 5px;
            }
            p {
              margin: 0;
              padding: 0;
              text-align: center;
              font-size: 12px;
              color:#e6b8b8;
            }
          }
        }
      }
    }
  }
  .paragraphNight{
     color: #E6B8B8;
  text-align: justify;
  text-indent: 1em;

  width: 80%;
  margin: auto;
  font-size: 16px;
  font-family: "Arial", "Hiragino Sans GB", "Microsoft YaHei",
    "WenQuanYi Micro Hei", sans-serif;
  margin-bottom: 36px;
  white-space: normal;
  word-break: break-all;
  overflow: hidden;
  }

  
</style>