<template>
<div>
   <div class="loading" v-show="isloading==true">
      <img src="../../img/猫吃猫.gif"  class="loadingpic">
      <p class="loadingWrite">loading...</p>
    </div>

  <div class="bookList" v-show="isloading==false">
    <div class="head">
      <div class="shang">
        <img src="../../img/返回.png" @click="fanhui" />
        <h3>书单详情</h3>
      </div>
    </div>

    <van-card
      v-for="(item, index) in books"
      :key="index"
      :desc="item.shortIntro"
      :title="item.title"
      :thumb="`https://statics.zhuishushenqi.com${item.cover}`"
      @click="bookdetail(item._id)"
    >
    </van-card>
    <div class="returnTop" @click="returnTop">
      <span>↑返回顶部</span>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      books: [],
      isloading:true,
    };
  },
  methods: {
    fanhui: function () {
      this.$router.back();
    },
    bookdetail: function (id) {
      this.$router.push({
        path: "/bookdetails",
        query: {
          id,
        },
      });
    },
    returnTop:function(){
      document.querySelector('.returnTop').onclick=function(){
 document.body.scrollTop = document.documentElement.scrollTop = 0;
      }
  },
  },
activated() {
  this.isloading=true;
  this.axios
        .get(
          "https://novel.kele8.cn/category-info?gender=" +
            this.$route.query.sex +
            "&type=hot&major=" +
            this.$route.query.major +
            "&minor=&start=0&limit=20"
        )
        .then((response) => {
          // console.log(response.data.male);
          console.log(response.data.books);
          this.books = response.data.books;
        }).finally(()=>{
          this.isloading=false;
          console.log(this.isloading)
        });
},

};
</script>

<style lang="less" scoped>
/deep/ .van-image__img {
  height: 120px;
}
.van-card {
  height: 140px;
  font-size: 16px;
  .van-card__header {
    height: 100%;
    .van-card__content {
      div {
        .van-card__desc,
        .van-ellipsis {
          height: 60px;
          max-height: 60px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          white-space: normal;
          font-size: 12px;
          margin-top: 15px;
          margin-left: 5px;
        }
      }
    }
    .van-card__thumb {
      height: 100%;
      .van-image {
        height: 100%;
      }
    }
  }
}
.van-card__price-currency {
  display: none;
}
.score {
  font-size: 12px;
  margin: 0;
  padding: 0;
  margin-left: 5px;
}
.van-tag,
.van-tag--plain,
.van-tag--danger {
  margin-top: 15px;
  margin-left: 5px;
}
.van-card__title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-left: 5px;
  margin-top: 25px;
}
.van-tag--danger.van-tag--plain {
  color: #666;
}

h4 {
  margin-bottom: 0px;
}
.head {
  height: 50px;
background:#E64D2E;
  width: 100%;
  .shang {
    height: 100%;
    // text-align: center;
    line-height: 50px;
    position: relative;
    

    img {
      height: 30px;
      position: absolute;
      top: 10px;
    }
    h3 {
      position: absolute;
      left: 42%;
      padding: 0;
      margin: 0;
      color: white;
    }
  }
}
// .van-card:nth-last-child(1) {
//   margin-bottom: 50px;
// }
.returnTop{
  width: 100%;
  height: 40px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 50px;
  text-align: center;
  color: #6392C5;
}
.loadingpic{
  
  width: 200px;
  height: 200px;
}
.loading{
  position: absolute;
  top: 40%;
  left: 25%;
  .loadingWrite{
    text-align: center;
    font-size: 14px;
  }
}
</style>