<template>
  <div class="army">
    <h3>军事战争<span @click="booklist(armyBook)">更多</span> </h3>
    <ul>
      <li v-for="(item, index) in armyBook.slice(0,4)" :key="index" class="books" @click="bookdetail(item._id)">
        <div class="head">
          <div class="img">
            <img :src="`https://statics.zhuishushenqi.com${item.cover}`" />
          </div>
        </div>
        <div class="bottom"><div class="bookname">{{item.title}}</div></div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["armyBook"],
  methods: {
    booklist:function (id) {
   this.$router.push({
        path: "/booklist",
        query: {
          id:JSON.stringify(id)
        },
      });
      
    },bookdetail: function (id) {
      this.$router.push({
        path: "/bookdetails",
        query: {
          id
        },
      });
    },
  },
};
</script>

