.army {
   margin-top: 10px;
   margin-bottom: 20px;
  .books {
    float: left;
    list-style: none;
    width: 25%;
    height: 150px;
    box-sizing: border-box;

    .head {
      height: 70%;
      // background: red;
      .img {
        width: 100%;
        height: 100%;
      }
      .img {
        margin: auto;
        width: 80%;
        img {
          width: 100%;
          height: 100%;
          box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
        }
      }
    }
    .bottom {
      width: 80%;
      height: 30%;
    //   background: brown;
      margin: auto;
      .bookname{
          padding: 0;
          margin: 0;
