<template>
  <div class="recommendList">
    <h3>重磅推荐</h3>
    <van-card
    
      v-for="(item, index) in hotBooks"
      :key="index"
      :desc="item.shortIntro"
      :title="item.title"
      :thumb="`https://statics.zhuishushenqi.com${item.cover}`"
      @click="bookdetail(item._id)"
    >
     <template #tags>
    <van-tag plain type="danger">{{item.minorCate}}</van-tag>
    <van-tag plain type="danger">收藏数:{{item.latelyFollower}}🔥</van-tag>
    <p class="score">评分:<strong> {{item.retentionRatio}} </strong>分</p>
  </template>

    </van-card>
  </div>
</template>

<script>
export default {
  name: "recommendList",
  props: ["hotBooks"],
  methods: {
      bookdetail: function (id) {
      this.$router.push({
        path: "/bookdetails",
        query: {
          id
        },
      });
    },
  },
};
</script>

<style lang="less">
/deep/ .van-image__img {
  height: 120px;
}
.van-card {
  height: 140px;
  font-size: 16px;
  .van-card__header {
    height: 100%;
    .van-card__content {
      div {
        .van-card__desc,
        .van-ellipsis {
          height: 60px;
          max-height: 60px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          white-space:normal;
          font-size: 12px;
          margin-top: 5px;
          margin-left: 5px;
        }
       
      }
    }
    .van-card__thumb {
      height: 100%;
      .van-image {
        height: 100%;
        border-radius: 8px;
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
        overflow: hidden;
      }
    }
  }
}
.van-card__price-currency{
