<template>
  <div class="female">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item><img src="https://plf-new.zhuishushenqi.com/management/images/20201127/2b8b5e6886ba472c9d6baea14de138b4.png" alt=""></van-swipe-item>
      <van-swipe-item><img src="https://plf-new.zhuishushenqi.com/management/images/20201218/c7108ad87b8e43ec8be92e278cd28db2.jpg" alt=""></van-swipe-item>
    </van-swipe>
    <FemaleOld :oldBook="oldBook" />
    <FemaleRelive :reliveBook="reliveBook" />
    <FemaleRich :richBook="richBook" />
  </div>
</template>

<script>
import FemaleOld from "@/components/FemaleOld.vue";
import FemaleRelive from "@/components/FemaleRelive.vue";
import FemaleRich from "@/components/FemaleRich.vue";
export default {
  components: { FemaleOld, FemaleRelive, FemaleRich },
  created() {
    // 古代
    if (localStorage.getItem("oldBook")) {
      this.oldBook = JSON.parse(localStorage.getItem("oldBook"));
      console.log("女生老本已存入本地储存");
    } else {
      this.axios
        .get(
          "https://novel.kele8.cn/category-info?gender=female&type=hot&major=%E5%8F%A4%E4%BB%A3%E8%A8%80%E6%83%85&minor=&start=0&limit=20"
        )
        .then((response) => {
          // console.log(response.data.books);
          this.oldBook = response.data.books;
          localStorage.setItem("oldBook", JSON.stringify(response.data.books));
        });
    }

    // 穿越
    if (localStorage.getItem("reliveBook")) {
      this.reliveBook = JSON.parse(localStorage.getItem("reliveBook"));
      console.log("女生穿越已存入本地储存");
    } else {
      this.axios
        .get(
          "https://novel.kele8.cn/category-info?gender=female&type=hot&major=%E7%8E%84%E5%B9%BB%E5%A5%87%E5%B9%BB&minor=&start=0&limit=20"
        )
        .then((response) => {
          console.log(response);
          this.reliveBook = response.data.books;
          localStorage.setItem(
            "reliveBook",
            JSON.stringify(response.data.books)
          );
        });
    }

    // 总裁
    if (localStorage.getItem("richBook")) {
      this.richBook = JSON.parse(localStorage.getItem("richBook"));
      console.log("女生总裁已存入本地储存");
    } else {
      this.axios
        .get(
          "https://novel.kele8.cn/category-info?gender=female&type=hot&major=%E7%8E%B0%E4%BB%A3%E8%A8%80%E6%83%85&minor=&start=0&limit=20"
        )
        .then((response) => {
          console.log(response);
          this.richBook = response.data.books;
          localStorage.setItem("richBook", JSON.stringify(response.data.books));
        });
    }
  },
  data() {
    return {
      oldBook: [],
      reliveBook: [],
      richBook: [],
    };
  },
};
</script>

