import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'

// import BookList from '../views/BookList.vue'

import bookShop from '../views/bookShop.vue'
import recommend from '../views/recommend.vue'
import female from '../views/female.vue'
import male from '../views/male.vue'
import publish from '../views/publish.vue'
import Search from '../views/Search.vue'
import classify from '../views/classify.vue'
import bookshelf from '../views/bookshelf.vue'
import BookList from '../views/BookList.vue'
import bookDetails from '../views/bookDetails.vue'
import chapters from '../views/chapters.vue'
import read from '../views/read.vue'
import bookclassify from '../views/BookClassify.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',

    component: bookShop,
    meta: { isShowNav: true },
    children: [
      {
        path: '/',
        name: "Recommend",
        component: recommend,
        meta: { isShowNav: true },
      },
      {
        path: '/male',
        name: "male",
        component: male, meta: { isShowNav: true },
      },
      {
        path: '/female',
        name: "female",
        component: female, meta: { isShowNav: true },
      },
      {
        path: '/search',
        name: "search",
        component: Search
      },
      {
        path: '/publish',
        name: "publish",
        component: publish, meta: { isShowNav: true },
      },
    ]
  }
  ,

  {
    path: '/classify',
    name: 'classify',
    component: classify,
    meta: { isShowNav: true },
  },
  {
    path: '/bookshelf',
    name: 'bookshelf',
    component: bookshelf,
    meta: { isShowNav: true },

  },
  
  {
    path: '/read',
    name: 'read',
    component: read,
    meta: { isShowNav: false },
  }
  ,
  {
    path: '/chapters',
    name: 'chapters',
    component: chapters,
    meta: { isShowNav: false },

  },
  {
    path: '/booklist',
    name: 'booklist',
    meta: { isShowNav: true },
    component: BookList
  },
  {
    path: '/bookdetails',
    name: 'bookdetails',
    component: bookDetails,
    meta: { isShowNav: false },
  },
  {
    path: '/bookclassify',
    name: 'bookclassify',
    component: bookclassify,
    meta: { isShowNav: false },
  }

]

const router = new VueRouter({
  routes
})

export default router
