<template>
  <div>
    <div class="loading" v-show="isloading == true">
      <img src="../../img/猫吃猫.gif" class="loadingpic" />
      <p class="loadingWrite">loading...</p>
    </div>
    <div class="chapters" v-show="isloading == false">
      <div class="head">
        <img src="../../img/返回.png" @click="fanhui" />

        <span style="margin-left: 15%">目录</span>

        <span class="meum">
          <router-link to="/" style="color: white"> 首页</router-link></span
        >
      </div>
  <!-- <van-list
  v-model="loading"
  :finished="finished"
  finished-text="没有更多了"
  @load="onLoad"
>
  <van-cell v-for="(item,index) in chapters " :key="index" :title="item.title" />
</van-list> -->
      <ul>
        <li
          v-for="(item, index) in chapters"
          :key="index"
          class="oneChapter"
          @click="
            read(item.id, currentBook,chapters,index)
          "
        >
          {{ item.title }} <span v-if="item.isVip" class="vip">VIP专属</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  activated() {
    this.isloading=true;
    this.currentBook = this.$route.query.currentBook;
    this.axios
      .get(
        "https://novel.kele8.cn/book-sources?view=summary&book=" +
          this.$route.query.bookId +
          ""
      )
      .then((response) => {
        console.log(response.data[0]._id);

        this.axios
          .get(
            "https://novel.kele8.cn/book-chapters/" + response.data[0]._id + ""
          )
          .then((re) => {
            console.log(re.data.chapters);
            this.chapters = re.data.chapters;
          });
      }).finally(() => {
        console.log("张杰加载完成");
        this.isloading = false;
      });
  },
  data() {
    return {
      chapters: [],
      isloading: true,
      currentBook: "",
    };
  },
  methods: {
    fanhui: function () {
      this.$router.back();
    },
    read: function (bookId, currentBook,chapters,chapterIndex) {
      this.$router.push({
        path: "/read",
        query: {
          bookId,
          currentBook,
          chapters,
          chapterIndex,
        },
      });
    },
  },
   
  
};
</script>

<style lang='less' scoped>
.head {
  width: 100%;
  height: 44px;
  background: #e64d2e;
  text-align: center;
  line-height: 44px;
  font-size: 20px;
  color: white;
  position: relative;
  img {
    position: absolute;
    height: 18px;
    top: 12px;
    left: 15px;
  }
}
ul {
  background: #faf7f5;
}
.oneChapter {
  position: relative;
  box-sizing: border-box;
  padding: 0 15px;
  height: 46px;
  line-height: 46px;
  border-bottom: 1px dotted #998d80;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 14px;
  color: #1d2c33;
  overflow: hidden;
}
.meum {
  float: right;
  font-size: 14px;
  padding: 0 15px 0 15px;
}
.vip {
  float: right;
  color: red;
}
.loadingpic {
  width: 200px;
  height: 200px;
}
.loading {
  position: absolute;
  top: 40%;
  left: 25%;
  .loadingWrite {
    text-align: center;
    font-size: 14px;
  }
}
</style>