<template>
  <div class="recommend">

    <RecommendList :hotBooks="hotBooks"></RecommendList>
    <RecommendCity :hotCity="hotCity"></RecommendCity>
    <RecommendGod :godBook="godBook"></RecommendGod>
    <RecommendGhost :ghostBook="ghostBook"></RecommendGhost>
  </div>
</template>

<script>
import RecommendList from "@/components/RecommendList.vue";
import RecommendCity from "@/components/RecommendCity.vue";
import RecommendGod from "@/components/RecommendGod.vue";
import RecommendGhost from "@/components/RecommendGhost.vue";

export default {
  components: { RecommendList, RecommendCity, RecommendGod, RecommendGhost },
  created() {
    if (localStorage.getItem("hotBooks")) {
      this.hotBooks = JSON.parse(localStorage.getItem("hotBooks"));
      console.log("热搜熟鸡已存入本地储存");
    } else {
      this.axios
        .get("https://novel.kele8.cn/rank/54d42d92321052167dfb75e3")
        .then((response) => {
          // console.log(response.data.ranking.books);
          response.data.ranking.books = response.data.ranking.books.slice(0, 8);
          // console.log( response.data.ranking.books)
          this.hotBooks = response.data.ranking.books;
          localStorage.setItem("hotBooks", JSON.stringify(response.data.ranking.books));
        });
    }

    if (localStorage.getItem("hotCity")) {
      this.hotCity = JSON.parse(localStorage.getItem("hotCity"));
      console.log("热搜城市已存入本地储存");
    } 
    else{
       this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=male&type=hot&major=%E9%83%BD%E5%B8%82&minor=&start=0&limit=10"
      )
      .then((response) => {
        // console.log(response.data.books);
        this.hotCity = response.data.books;
        localStorage.setItem("hotCity", JSON.stringify(response.data.books));
      });
    }

     if (localStorage.getItem("godBook")) {
      this.godBook = JSON.parse(localStorage.getItem("godBook"));
      console.log("热搜神书已存入本地储存");
    } 
    else{
       this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=male&type=hot&major=%E4%BB%99%E4%BE%A0&minor=&start=0&limit=20"
      )
      .then((response) => {
        // console.log(response.data.books);
        this.godBook = response.data.books;
         localStorage.setItem("godBook", JSON.stringify(response.data.books));
      });
    }

   if (localStorage.getItem("ghostBook")) {
      this.ghostBook = JSON.parse(localStorage.getItem("ghostBook"));
      console.log("热搜鬼书已存入本地储存");
    } 
    else{
      this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=male&type=hot&major=%E7%81%B5%E5%BC%82&minor=&start=0&limit=20"
      )
      .then((response) => {
        // console.log(response.data.books);
        this.ghostBook = response.data.books;
         localStorage.setItem("ghostBook", JSON.stringify(response.data.books));

      });
    }

   
    

    //书单检修中
    //  this.axios
    // .get(
    //   "https://novel.juhe.im/booklists:54d42d92321052167dfb75e3"
    // )
    // .then((response) => {
    //   console.log(response.data.books);
    //   // this.ghostBook = response.data.books.splice(0, 4);
    // });
  },
  data() {
    return {
      hotBooks: [],
      hotCity: [],
      godBook: [],
      ghostBook: [],
    };
  },
};
</script>

