  margin-top: 10px;
  height: 400px;
}

.van-swipe-item {
  color: #000;
  font-size: 12px;
  // line-height: 150px;
  text-align: center;
  background-color: rgb(249, 249, 249);
}
.writter {
  color: #666;
}
.account {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  height: 50px;
  color: #999;
}
.title {
  font-size: 16px;
  color: #333;
}

</style>