<template>
  <div>
<div class="loading" v-show="isloading==true">
      <img src="../../img/猫吃猫.gif"  class="loadingpic">
      <p class="loadingWrite">loading...</p>
    </div>
  <div class="bookDetails" v-show="isloading==false">
   
    <div class="head">
      <div class="shang">
        <img src="../../img/返回.png" @click="fanhui" />
        <h3>书籍详情</h3>
      </div>
    </div>
    <van-card
      :title="currentBook.title"
      :thumb="`https://statics.zhuishushenqi.com${currentBook.cover}`"
    >
      <template #tags>
        <div class="des">
          <p>作者:{{ currentBook.author }}</p>
          <p>分类:{{ currentBook.majorCate }}</p>
          <p>字数:{{ currentBook.wordCount | formatPlayCount }}</p>
          <p>收藏数:{{ currentBook.totalFollower | formatPlayCount }}</p>
          <p>{{ currentBook.copyrightDesc }}</p>
        </div>
      </template>
      <template #footer>
        <van-button size="mini" @click="read(currentBook._id,currentBook)"
          >立即阅读</van-button
        >
        <van-button size="mini">
          <van-cell is-link @click="showPopup"> 下载整本</van-cell></van-button
        >
        <van-button
          type="danger"
          @click="removeBookrack(JSON.stringify(currentBook))"
          v-if="bookrack.find((id) => id == JSON.stringify(currentBook))"
          >移除书架</van-button
        >
        <van-button type="default" @click="addBookrack(JSON.stringify(currentBook))" v-else
          >加入书架</van-button
        >
      </template>
    </van-card>

    <div class="new">
      <span class="title"> 最新章</span>
      <span class="content">{{ currentBook.lastChapter }}</span>
    </div>
    <div class="simple">
      <h4>内容简介</h4>
      <p>{{ currentBook.longIntro }}</p>
    </div>
    <div class="review">
      <h4>最新评论</h4>
      <ul style="min-height:170px">
        <li class="oneReview" v-for="(item, index) in review" :key="index">
          <div class="reviewName">
            <img
              :src="`https://statics.zhuishushenqi.com${item.author.avatar}`"
              class="avatar"
            />
            <span class="authorName"> {{ item.author.nickname }}</span>
          </div>
          <div class="bottom">
            {{ item.content }}
          </div>
          <span class="lastTime">{{ item.updated.slice(0, 10) }}</span>
        </li>
      </ul>
      <!-- <div class="returnTop">
      返回顶部
    </div> -->
    </div>

    <van-popup v-model="show">抱歉，该功能还未开通。请等待后续通知。</van-popup>
    <div class="returnTop">
      <span @click="returnTop">↑返回顶部</span>
    </div>
  </div>
   </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  activated() {
    this.isloading=true;
    this.axios
      .get("https://novel.kele8.cn/book-info/" + this.$route.query.id + "")
      .then((response) => {
        console.log(response.data);
        this.currentBook = response.data;
      });

    this.axios
      .get(
        "https://novel.kele8.cn/book/short-reviews?book=" +
          this.$route.query.id +
          "&limit=10"
      )
      .then((response) => {
        console.log(response.data.docs);
        this.review = response.data.docs;
      }).finally(()=>{
        this.isloading=false;
        console.log(this.isloading)
      });
    console.log(this.$route.query.id);
  },
  methods: {
    fanhui: function () {
      this.$router.back();
    },
    showPopup() {
      this.show = true;
    },

    read: function (bookId,currentBook) {
      this.$router.push({
        path: "/chapters",
        query: {
          bookId,
         currentBook:JSON.stringify(currentBook),
        },
      });
    },
    returnTop: function () {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    },
    ...mapMutations(["addBookrack", "removeBookrack"]),
  },
  data() {
    return {
      currentBook: [],
      review: [],
      show: false,
      bookInfo: null,
      isloading:0,
    };
  },
  filters: {
    formatPlayCount: function (value) {
      return (value / 10000).toFixed(1) + "万";
    },
  },
  computed: mapState(["bookrack"]),
};
</script>

<style lang="less" scoped>
.head {
  height: 50px;
  background: #e64d2e;
  width: 100%;
  .shang {
    height: 100%;
    // text-align: center;
    line-height: 50px;
    position: relative;

    img {
      height: 30px;
      position: absolute;
      top: 10px;
    }
    h3 {
      position: absolute;
      left: 42%;
      padding: 0;
      margin: 0;
      color: white;
    }
  }
}
.van-card__title {
  font-weight: 700;
  font-size: 16px;
}
p {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: rgb(136, 136, 136);
  padding-left: 7px;
  margin-top: 5px;
}
.van-card__footer {
  position: relative;
  padding: 0;
  margin-top: 20px;
  height: 115px;
  &::after {
    display: block;
    clear: both;
    content: "";
  }
}
.van-button {
  width: 45%;
  position: absolute;
  height: 34px;
  border-radius: 8px;
  line-height: 34px;
  font-size: 16px;
}
.van-button:nth-child(1) {
  background: #3c98c9;
  left: 0;
  color: #fff;
}
.van-button:nth-child(2) {
  background: #fff;
  left: 50%;
  color: #3c98c9;
  box-sizing: border-box;
  border: 1px solid #3c98c9;
}
.van-button:nth-child(3) {
  margin-top: 15px;
  left: 0%;
  color: #3c98c9;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  top: 34px;
  background: #faf7f5;
  width: 95%;
}

.new {
  background: rgb(235, 230, 220);
  height: 50px;
  width: 90%;
  margin-left: 5%;
  margin-top: 110px;
  position: relative;
  font-size: 16px;
  color: #3a98c9;
  display: inline-block;
}
.title {
  border: 1px solid #777;
  color: #d66464;
  margin-top: 20px;
  margin-left: 5%;
  display: inline-block;
  font-size: 12px;
}
.content {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 70%;
  position: absolute;
  left: 80px;
  top: 18px;
}
.simple {
  width: 90%;
  margin: auto;
  background: #faf7f5;
  min-height: 100px;
  padding-top: 7px;
  h4 {
    margin: 0;
    padding: 0;
  }
}
.review {
  width: 90%;
  margin: auto;
  background: #faf7f5;
  .oneReview {
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    height: 105px;

    .reviewName {
      color: #6392c5;
      font-size: 14px;
      position: relative;
      height: 40px;
    }
  }
}
.avatar {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  position: absolute;
}
.authorName {
  display: inline-block;
  position: absolute;
  left: 12%;
  top: 15px;
}
.bottom {
  color: #545454;
  font-size: 14px;
  vertical-align: bottom;
  height: 40px;
  margin: 0;
  padding: 0;
}
.lastTime {
  position: absolute;
  left:75%;
  display: block;
  font-size: 14px;
  color: #545454;
}
.van-cell {
  background: none;
  height: 100%;
  width: 100%;
}
.van-popup {
  width: 70%;
  height: 100px;
  border-radius: 10px;
  text-align: center;
  line-height: 100px;
  font-weight: 600;
  padding: 15px;
}
.returnTop {
  width: 100%;
  height: 40px;
  // border: 1px solid rgba(0, 0, 0, 0.1);
  // margin-bottom: 60px;
  text-align: center;
  color: #6392c5;
}
.van-icon-arrow::before {
  content: "";
}
.loadingpic{
  
  width: 200px;
  height: 200px;
}
.loading{
  position: absolute;
  top: 40%;
  left: 25%;
  .loadingWrite{
    text-align: center;
    font-size: 14px;
  }
}
</style>