<template>
  <div class="male">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
  <van-swipe-item><img src="https://plf-new.zhuishushenqi.com/management/images/20201218/0d8dc8bbb17a4c9793706ab12d979697.jpg" alt=""></van-swipe-item>
  <van-swipe-item><img src="https://plf-new.zhuishushenqi.com/management/images/20201218/12ddcc817cdf49f787959bb53e21b584.png" alt=""></van-swipe-item>
</van-swipe>
<MaleDoctor :doctorBook="doctorBook" />
<MaleCity :cityBook="cityBook" />
<MaleArmy :armyBook="armyBook" />
  </div>
</template>

<script>
import MaleDoctor from '@/components/MaleDoctor.vue'
import MaleCity from '@/components/MaleCity.vue'
import MaleArmy from '@/components/MaleArmy.vue'
export default {
  components:{MaleDoctor,MaleCity,MaleArmy},
  created() {
    if(localStorage.getItem("doctorBook")){
      this.doctorBook=JSON.parse(localStorage.getItem("doctorBook"));
      console.log("男生医本已存入本地缓存");
    }
  else{
this.axios
      .get("https://novel.kele8.cn/category-info?gender=male&type=hot&major=%E7%8E%84%E5%B9%BB&minor=&start=0&limit=20")
      .then((response) => {
        // console.log(response.data.books);
        this.doctorBook=response.data.books
        localStorage.setItem("doctorBook",JSON.stringify(response.data.books))
        
      });
  }

  if(localStorage.getItem("cityBook")){
    this.cityBook=JSON.parse(localStorage.getItem("cityBook"));
    console.log("男生市本已存入本地缓存");
  }
    else{
this.axios
      .get("https://novel.kele8.cn/category-info?gender=male&type=hot&major=%E9%83%BD%E5%B8%82&minor=&start=0&limit=20")
      .then((response) => {
        // console.log(response);
        this.cityBook=response.data.books;
        localStorage.setItem("cityBook",JSON.stringify(response.data.books));
        
      });
    }

      if(localStorage.getItem("armyBook")){
        this.armyBook=JSON.parse(localStorage.getItem("armyBook"));
        console.log("男生军本已存入本地缓存");
      }
      else{
          this.axios
      .get("https://novel.kele8.cn/category-info?gender=male&type=hot&major=%E5%86%9B%E4%BA%8B&minor=&start=0&limit=20")
      .then((response) => {
        // console.log(response);
        this.armyBook=response.data.books
         localStorage.setItem("armyBook",JSON.stringify(response.data.books));
        
      });
      }

     
  },
  data() {
    return {
      doctorBook:[],
      cityBook:[],
      armyBook:[]
    }
  },
  

}
</script>

<style lang="less" scoped >
 .my-swipe .van-swipe-item {
    color: black;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
  }
  .male{
    margin-top: 5px;
  }
  .my-swipe .van-swipe-item[data-v-c066f2b2]{
    background: #E64D2E;
    line-height: 0;
  }
  img{
  width: 100%;
  box-sizing: 100%;
}
