<template>
  <div class="search">
    <div class="head">
      <div class="shang">
        <img src="../../img/返回.png" @click="fanhui" />
        <input
          type="text"
          placeholder="搜索你想要的书籍、作者"
          @input="submit"
          v-model="b"
        />
        <button @click="clearAll"></button>
      </div>
    </div>
    <div class="searchList" v-show="isShowList">
      <ul class="result">
        <li
          v-for="(item, index) in resultBooks"
          :key="index"
          class="resultItem"
          @click="showDeatil(item.title)"
        >
          {{ item.title }} -- {{ item.author }}
        </li>
      </ul>
    </div>
    <div class="body" v-show="isShowBody">
      <h5>大家都在搜</h5>
      <ul class="hotwordlist">
        <li
          class="hotwords"
          v-for="(item, index) in hotWord"
          :key="index"
          @click="change(item.word)"
        >
          {{ item.word }}
        </li>
      </ul>
      <h5>搜索记录  <span class="clearHis" @click="clearHis"></span></h5>
      <ul class="hisUl">
        <li
          v-for="(item, index) in historyRecord"
          :key="index"
          class="recordItem"
          @click="change(item)"
        >
          {{ item }}
        </li>
      </ul>
    </div>
    
    <div class="searchDeatil" v-show="isShowDetail">
      <div class="loading" v-show="isloading==true">
      <img src="../../img/猫吃猫.gif"  class="loadingpic">
      <p class="loadingWrite">loading...</p>
    </div>
      <van-card
        v-for="(item, index) in resultBooks"
        :key="index"
        :desc="item.shortIntro"
        :title="item.title"
        :thumb="`https://statics.zhuishushenqi.com${item.cover}`"
        @click="bookdetail(item._id)"
        v-show="isloading==false"
      >
      </van-card>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    this.axios.get("https://novel.kele8.cn/search-hotwords").then((response) => {
      // console.log(response.data.male);
      console.log(response.data.searchHotWords);
      this.hotWord = response.data.searchHotWords.slice(0, 10);
    });

    // if(localStorage.getItem("record")){
    //   this.historyRecord=localStorage.getItem("record")
    // }

  },
  methods: {
    fanhui: function () {
      this.$router.back();
    },
    submit: function () {
      // console.log(111);
      if (this.b=="") {
        this.isShowBody = true;
        this.isShowList = false;
        return;
      }
      else{
        this.isloading=true;
      this.isShowBody = false;
      this.isShowList = true;
      if (!this.timeOut) {
        clearTimeout(this.timeOut);
      }
      this.timeOut = setTimeout(() => {
        this.axios
          .get("https://novel.kele8.cn/search?keyword=" + this.b + "")
          .then((response) => {
            console.log(response.data.books);
            this.resultBooks = response.data.books.slice(0, 8);
          }).finally(()=>{
            this.isloading=false;
            console.log("搜索完成")
          });
        clearTimeout(this.timeOut);
        if (this.historyRecord.indexOf(this.b) == -1&&this.b!='') {
          this.historyRecord.push(this.b);
          localStorage.setItem("record", this.historyRecord);
        }
      }, 500);
      }
    },
    showDeatil: function (word) {
      this.isShowBody = false;
      this.isShowList = false;
      this.isShowDetail = true;
      this.b = word;
    },
    bookdetail: function (id) {
      this.$router.push({
        path: "/bookdetails",
        query: {
          id,
        },
      });
    },
    change: function (word) {
      this.b = word;
      this.submit();
      this.isShowDetail = true;
      this.isShowBody = false;
      this.isShowList = false;
    },
    clearAll: function () {
      this.b = "";
      this.isShowList = false;
      this.isShowBody = true;
      this.isShowDetail = false;
    },
    clearHis:function(){
      this.historyRecord=[];
        // localStorage.clear();
    },
  },
  data() {
    return {
      hotWord: [],
      isShowBody: true,
      isShowList: false,
      b: "",
      timeOut: null,
      resultBooks: [],
      isShowDetail: false,
      historyRecord: [],
      isloading:true,
    };
  },
};
</script>

<style lang="less" scoped>
.head {
  height: 50px;
  background: #e64d2e;
  width: 100%;
  .shang {
    height: 100%;
    // text-align: center;
    line-height: 50px;
    position: relative;

    img {
      height: 30px;
      position: absolute;
      top: 10px;
      display: inline-block;
    }
    h3 {
      position: absolute;
      left: 42%;
      padding: 0;
      margin: 0;
      color: white;
    }
    input {
      border: none;
      height: 28px;
      line-height: 28px;
      font-size: 12px;
      vertical-align: middle;
      box-sizing: border-box;
      color: #6c6358;
      background: #fff;
      border-radius: 14px;
      width: 70%;
      padding: 0 51px 0 15px;
      position: absolute;
      top: 10px;
      left: 15%;
    }
    button {
      width: 12px;
      height: 12px;
      border: none;
      vertical-align: middle;
      background: url(../../img/清除.png);
      position: absolute;
      left: 80%;
      z-index: 8;
      background-size: 100%;
      top: 18px;
    }
  }
}
h5 {
  background: #f5f5f5;
  padding: 0;
  margin: 0;
  height: 37.5px;
  color: #807a73;
  font-size: 14px;
  margin: auto;
  line-height: 37.5px;
  padding-left: 10px;
  padding-right: 10px;
}
.hotwordlist {
  padding: 15px;

  .hotwords {
    color: #aba59a;
    background: white;
    font-size: 14px;
    padding: 0 10px;
    line-height: 28px;
    height: 28px;
    margin-right: 15px;
    margin-bottom: 15px;
    display: inline-block;
    border: 1px solid #c8c8c8;
    border-radius: 4px;
    position: relative;
  }
}
.result {
  top: 60px;
  left: 0px;
  right: 0px;
  clear: both;
  overflow: scroll;
  background: #fff;
  padding-left: 15px;
  height: 450px;
  .resultItem {
    color: #6c6358;
    font-size: 16px;
    padding-right: 50px;
    line-height: 50px;
    height: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
}
.hisUl {
  padding: 15px;
  .recordItem {
    color: #aba59a;
    background: white;
    font-size: 14px;
    padding: 0 10px;
    line-height: 28px;
    height: 28px;
    margin-right: 15px;
    margin-bottom: 15px;
    display: inline-block;
    border: 1px solid #c8c8c8;
    border-radius: 4px;
    position: relative;
  }
}
.clearHis{
  float: right;
  width: 18px;
  height: 18px;
  background: url(../../img/delete.png);
  margin-top: 8px;
  background-size: 100%;
}
.loadingpic{
  
  width: 200px;
  height: 200px;
}
.loading{
  position: absolute;
  top: 40%;
  left: 25%;
  .loadingWrite{
    text-align: center;
    font-size: 14px;
    color: #666;
  }
}
</style>