      :thumb="`https://statics.zhuishushenqi.com${oldBook[0].cover}`"
      v-if="oldBook[0]"
      @click="bookdetail(oldBook[0]._id)"
    >
    </van-card>
    <ul>
      <li v-for="(item,index) in oldBook.slice(1,5)" :key=index @click="bookdetail(item._id)">  
        <img :src="`https://statics.zhuishushenqi.com${item.cover}`" alt="">
        
        <p>{{item.title}}</p></li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["oldBook"],
  methods: {
    booklist:function (id) {
   this.$router.push({
        path: "/booklist",
        query: {
          id:JSON.stringify(id)
        },
      });
      
    },
    bookdetail: function (id) {
      this.$router.push({
        path: "/bookdetails",
        query: {
          id
        },
      });
    },
  },
