<template>
  <div class="publish">
      <div class="publishBook">
        <h3>发行小说</h3>
        <ul>
          <li v-for="(item,index) in publishBook" :key=index @click="bookdetail(item._id)">
            <img :src="`https://statics.zhuishushenqi.com${item.cover}`" alt="">
            <p>{{item.title}} </p>

          </li>
        </ul>
      </div>
  </div>
</template>

<script>
export default {
created() {

  if(localStorage.getItem("publishBook")){
    this.publishBook=JSON.parse(localStorage.getItem("publishBook"));
  }
  else{
     this.axios
      .get(
        "https://novel.kele8.cn/category-info?gender=press&type=hot&major=%E5%87%BA%E7%89%88%E5%B0%8F%E8%AF%B4&minor=&start=0&limit=50"
      )
      .then((response) => {
        // console.log(response.data.books);
        this.publishBook = response.data.books;
        localStorage.setItem("publishBook",JSON.stringify(response.data.books));
      });
  }
 
},
data() {
  return {
    publishBook:[]
  }
},
methods: {
  bookdetail: function (id) {
      this.$router.push({
        path: "/bookdetails",
        query: {
          id
        },
      });
    },
},
}
</script>

<style scoped lang="less">
.publish{
  margin-bottom:10px;
  background:#FAFAFA;

&&::after{
  content: "";
  display: block;
  clear: both;
}
li{
  float: left;
  width: 30vw;
  height: 180px;
  // background: gray;
  margin: 10px 5px;
  overflow: hidden;
  img{
    height: 66.6%;
    display: block;
    margin: auto;
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
    width: 66.6%;
    

  }

  &:nth-child(3n-2){
    margin-left: 10px;
  }
  p{text-align: center;
  margin-top: 2px;
  padding: 0 7px;
   white-space: nowrap; 
  overflow: hidden;
  text-overflow:ellipsis;
  }
}
}
h3 {
  padding-left: 20px;
  height: 28px;
  line-height: 28px;
  margin-bottom: 16px;
  font-weight: bold;

  span {
    float: right;
    font-size: 13px;
    color: #999;
    padding:0 17px;
    font-weight: normal;
    box-sizing: border-box;
    border: 1px solid #f0f0f0;
    border-radius: 28px;

    
  }
}

</style>