    <van-card
      v-for="(item, index) in reliveBook.slice(0,5)"
      :key="index"
      :desc="item.shortIntro"
      :title="item.title"
      :thumb="`https://statics.zhuishushenqi.com${item.cover}`"
      @click="bookdetail(item._id)"
    >
     
    </van-card>
  </div>
</template>

<script>
export default {
  name: "MaleCity",
  props: ["reliveBook"],methods: {
    booklist:function (id) {
   this.$router.push({
        path: "/booklist",
        query: {
          // id,
          id:JSON.stringify(id)

        },
      });
      
    },
    bookdetail: function (id) {
      this.$router.push({
        path: "/bookdetails",
        query: {
          id
        },
      });
    },
  },
};
</script>

