<template>
  <div class="recommendcity">
    <h3>都市言情 <span @click="booklist(hotCity)">更多</span></h3>
    <ul>
      <li v-for="(item, index) in hotCity.slice(0,4)" :key="index" class="books" @click="bookdetail(item._id)">
        <div class="head">
          <div class="img">
            <img :src="`https://statics.zhuishushenqi.com${item.cover}`" />
          </div>
        </div>
        <div class="bottom">
          <div class="bookname">{{ item.title }}</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["hotCity"],
  methods: {
    booklist: function (id) {
      this.$router.push({
        path: "/booklist",
        query: {
          id:JSON.stringify(id)
        },
      });
    },
    bookdetail: function (id) {
      this.$router.push({
        path: "/bookdetails",
        query: {
          id
        },
      });
    },
  },
};
</script>

<style scoped lang="less">
.recommendcity {
  margin-top: 10px;
  .books {
    float: left;
    list-style: none;
    width: 25%;
    height: 150px;
    box-sizing: border-box;

    .head {
      height: 70%;
      // background: red;
      .img {
        width: 100%;
        height: 100%;
      }
      .img {
        margin: auto;
        width: 80%;
        img {
          width: 100%;
          height: 100%;
          // border-radius: 8px;
          // box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
        }
      }
    }
    .bottom {
      width: 80%;
      height: 30%;
      //   background: brown;
      margin: auto;
      .bookname {
        padding: 0;
        margin: 0;
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        white-space: normal;
      }
    }
  }
}
h3 {
  padding-left: 20px;
  height: 28px;
  line-height: 28px;
  margin-bottom: 16px;
  font-weight: bold;

  span {
    float: right;
    font-size: 13px;
    color: #999;
    padding:0 17px;
    font-weight: normal;
    box-sizing: border-box;
    border: 1px solid #f0f0f0;
    border-radius: 28px;

    
  }
}
ul{
  min-height: 140px;
}
</style>