<template>
<div>
  <div class="loading" v-show="isloading==true">
      <img src="../../img/猫吃猫.gif"  class="loadingpic">
      <p class="loadingWrite">loading...</p>
    </div>

  <div class="classify" v-show="isloading==false">
    <h3>书籍分类</h3>

    <van-tabs v-model="active">
      <van-tab title="男生">
        <ul>
          <div class="bg"></div>
          <li
            v-for="(item, index) in male"
            :key="index"
            class="item"
            @click="boy(male[index].name)"
          >
            {{ item.name }}
          </li>
        </ul>
      </van-tab>
      <van-tab title="女生">
        <ul>
          <div class="bg"></div>
          <li v-for="(item, index) in female" :key="index" class="item" @click="girl(female[index].name)"> 
            {{ item.name }}
          </li>
        </ul>
      </van-tab>
    </van-tabs>
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 2,
      male: [],
      female: [],
      all: [],
      isloading:true,
    };
  },
  created() {
    this.axios.get("https://novel.kele8.cn/categories").then((response) => {
      // console.log(response.data.male);
      this.male = response.data.male;
      this.female = response.data.female;
      this.all = response.data;
    }).finally(()=>{
      this.isloading=false
    });
  },
  methods: {
    boy: function (major) {
      this.$router.push({
        path: "/bookclassify",
        query: {
          major,
          sex: "male",
        },                         
      });
    },
    girl:function(major){
      this.$router.push({
        path: "/bookclassify",
        query: {
          major,
          sex: "female",
        },
      })

    }
  },
};
</script>

